import React, { createContext, useState, useEffect } from "react";
import firebase from "../assets/firebase";
import "firebase/database";

export const AppContext = createContext();

const Store = ({ children }) => {
  const [words, setWords] = useState([]);

  useEffect(() => {
    let unsubscribe;

    const wordsRef = firebase.database().ref("/words");
    const watch = () => {
      unsubscribe = wordsRef.on("value", (snapshot) => {
        const wordsValue = snapshot.val();
        const temporaryData = [];
        for (let id in wordsValue) {
          temporaryData.push(wordsValue[id]);
        }
        setWords(temporaryData);
      });
    };
    watch();
    return () => unsubscribe();
  }, []);

  return (
    <AppContext.Provider
      value={{
        words,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default Store;
