import React, { useState, useContext, useEffect } from "react";
import Back from '../Back/Back';
import { AppContext } from "../../store/Store";
import Card from "../Card/Card";

const RandomTen = ({ match }) => {
  const { words } = useContext(AppContext);

  const [filteredWords, setFilteredWords] = useState([]);

  useEffect(() => {
    const setNewData = () => {
      const temporaryData = [];
      words.map((item) => temporaryData.push(item));
      temporaryData.sort(() => Math.random() - 0.5);
      temporaryData.splice(match.params.flashlightCategory, temporaryData.length);
      setFilteredWords(temporaryData);
    };

    setNewData();
  }, []);
  
  const listOfCards = filteredWords.map((item) => (
    <Card key={item.plValue} PL_WORD={item.plValue} ENG_WORD={item.engValue} />
  ));

  return (
    <div className="RandomTen">
      <Back />
      {words.length && listOfCards}
    </div>
  );
};

export default RandomTen;
