import React, { useState } from "react";
import Back from '../Back/Back';
import firebase from "../../assets/firebase";
import "firebase/database";

const AddWords = () => {
  const [engValue, setEngValue] = useState("");
  const [plValue, setPlValue] = useState("");
  const [categoryValue, setCategoryValue] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();

    if ((engValue, plValue, categoryValue)) {
      firebase
        .database()
        .ref("/words/" + plValue)
        .set({
          engValue,
          plValue,
          categoryValue,
        });
    } else return
    
    setEngValue('');
    setPlValue('');
    setCategoryValue('');
  };

  return (
    <div className="AddWords">
      <Back />

      <form className="AddWords__form">
        <h1 className="AddWords__formHeader">Dodaj słówko</h1>

        <label className="AddWords__engWordLabel">
          Eng
          <input
            value={engValue}
            onChange={(e) => setEngValue(e.target.value)}
            type="text"
            className="AddWords__engWordInput"
          />
        </label>

        <label className="AddWords__plWordLabel">
          Pl
          <input
            value={plValue}
            onChange={(e) => setPlValue(e.target.value)}
            type="text"
            className="AddWords__plWordInput"
          />
        </label>

        <label className="AddWords__wordCategoryLabel">
          Kategoria
          <input
            value={categoryValue}
            onChange={(e) => setCategoryValue(e.target.value)}
            type="text"
            className="AddWords__wordCategoryInput"
          />
        </label>

        <button onClick={handleSubmit} className="AddWords__formButton">
          DODAJ
        </button>
      </form>
    </div>
  );
};

export default AddWords;
