import React, {useState} from 'react';

const Card = ({PL_WORD, ENG_WORD}) => {
    const [isFlipped, setIsFlipped] = useState(false);

    const flipHandle = () => {
        setIsFlipped(!isFlipped);
      };

    return ( 
        <div onClick={flipHandle} className="Card">
        <div
          className={
            isFlipped
              ? "Card__cardInner Card__cardInner--isFlipped"
              : "Card__cardInner"
          }
        >
          <div className="Card__cardFace Card__cardFace--front">
            <h2>{PL_WORD}</h2>
          </div>
          <div className="Card__cardFace Card__cardFace--back">
            <div className="Card__cardContent">
              <div className="Card__cardHeader">
                <h2>{ENG_WORD}</h2>
              </div>
              <div className="Card__cardBody"></div>
            </div>
          </div>
        </div>
      </div>
     );
}
 
export default Card;