import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "../Home/Home";
import AddWords from "../AddWords/AddWords";
import RandomTen from "../RandomTen/RandomTen";
import Store from "../../store/Store";

function App() {
  return (
    <Store>
      <Router>
        <div className="App">
          <Switch>
            <Route exact path="/english_memory" component={Home} />
            <Route path="/english_memory/addWords" component={AddWords} />
            <Route path="/english_memory/flashlights/:flashlightCategory" component={RandomTen} />
          </Switch>
        </div>
      </Router>
    </Store>
  );
}

export default App;
